<script>
  import { onMount } from "svelte";
  import parallax from "./parallax";

  function startParallax() {
    let $ = document.querySelector.bind(document);

    let blob1 = $("#blob-1");
    let blob2 = $("#blob-2");
    let blob3 = $("#blob-3");

    parallax(
      (lastScrollY, prefix, pos, setTickingFalse) => {
        let relativeY = lastScrollY / 2000;
        let xmultiplier = window.innerWidth / 960;
        // big blue top corner
        prefix(
          blob1.style,
          "Transform",
          "translate3d(" +
            ((960 * xmultiplier).toFixed() - blob1.width).toFixed() +
            "px," +
            pos(0, -3900, relativeY, 0) +
            "px, 0)"
        );
        // big red
        prefix(
          blob2.style,
          "Transform",
          "translate3d(" +
            300 * xmultiplier +
            "px," +
            pos(604, -1040, relativeY, 0) +
            "px, 0)"
        );

        // first small blue
        prefix(
          blob3.style,
          "Transform",
          "translate3d(" +
            348 * xmultiplier +
            "px," +
            pos(104, -500, relativeY, 0) +
            "px, 0)"
        );

        setTickingFalse();
      },
      function() {
        blob1.classList.add("force-show-sm");
        blob2.classList.add("force-show");
        blob3.classList.add("force-show");
      },
      startParallax
    );
  }
  onMount(async () => {
    startParallax();
  });

  import Nav from "../components/Nav.svelte";
  import Footer from "../components/Footer.svelte";
</script>


